@use '../variables';

.modal-container {
  display: none;

  &__overlay {
    display: flex;
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgba(18, 18, 19, 0.6);
  }

  &--active {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    position: fixed;
    z-index: variables.$modal-z-index;
    right: 0;
    left: 0;
    top: 0;
  }
}

.modal {
  padding: 48px 32px;
  position: relative;
  box-sizing: border-box;
  background: #fff;
  width: 450px;
  margin: auto;
  border-radius: 4px;
  box-shadow: 0 16px 32px rgba(0, 0, 0, 0.07),
  0 12px 16px rgba(0, 0, 0, 0.07),
  0 8px 8px rgba(0, 0, 0, 0.07),
  0 4px 4px rgba(0, 0, 0, 0.07),
  0 2px 2px rgba(0, 0, 0, 0.07);

  &__close {
    position: absolute;
    width: 32px;
    height: 32px;
    right: 12px;
    top: 12px;
    background: url(../../../img/close.svg) center center no-repeat;
    cursor: pointer;
  }

  &__title {
    color: variables.$typo-primary;
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;

    &--center {
      text-align: center;
    }
  }

  &__icon {
    display: flex;
    justify-content: center;
    padding-bottom: 13px;
  }
}