@use '../variables';
@use '../mixins';

.sidebar {
  //flex: 1 0 265px;
  width: 265px;
  height: 100%;
  background: variables.$back-primary;
  position: fixed;
  left: 0;
  z-index: variables.$sidebar-z-index;

  &__menu {
    list-style: none;
    padding: 0;
    margin: 22px 0 0;
  }

  &__menu-item {
    @include mixins.default-transition;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #fff;

    box-sizing: border-box;
    margin-bottom: 4px;

    &:hover {
      background-color: lighten(variables.$back-primary, 3%);
    }

    &--active {
      color: variables.$back-primary;
      background: variables.$back-white;

      &:hover {
        background: variables.$back-white;
      }
    }
  }

  &__menu-item-link {
    color: inherit;
    display: flex;
    box-sizing: border-box;
    padding: 8px 22px;
    text-decoration: none;
  }
}