@use "../variables";

.breadcrumbs {
  font-size: 18px;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;

  &__link {
    text-decoration: none;
    color: variables.$typo-secondary;
    margin-right: 48px;
    margin-bottom: 10px;
    position: relative;

    &:hover {
      cursor: pointer;
      color: #0078D2;
      text-decoration: none;
    }

    &:after {
      content: '';
      background: url(../../../img/bread_arrow.svg);
      width: 8px;
      height: 12px;
      display: block;
      position: absolute;
      right: -30px;
      top: 2px;
      bottom: 0;
      margin: auto;
      pointer-events: none;
    }

    &--disabled {
      color: unset;

      &:hover {
        cursor: unset;
        color: unset;
        text-decoration: none;
      }
    }
  }

  &__item {
    margin-bottom: 10px;
  }
}
