@use '../variables';
@use '../mixins';

.form {

  &--edit {

    & > .form__group, & > .form__actions {
      max-width: 410px;
    }

    & > .form__group--max-width {
      max-width: none;
    }
  }

  &__group {
    display: flex;
    flex-direction: column;
    position: relative;

    & label {
      color: variables.$typo-secondary;
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 4px;
    }

    &:not(:last-child) {
      margin-bottom: 16px;
    }

    &--inline {
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__group-header {
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: #002033;
    margin: 6px 0;
  }

  &__group-description {
    font-weight: 300;
    font-size: 12px;
    line-height: 16px;
    color: rgba(0, 32, 51, 0.6);
  }

  &__group-text{
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: rgba(0, 32, 51, 0.6);
  }

  &__group-label {
    display: flex;
    justify-content: space-between;
  }

  &__group-label-link {
    color: #007bff;
    text-decoration: none;
    margin-bottom: 4px;

    &:hover {
      text-decoration: underline;
    }
  }

  &__actions {
    display: flex;
    column-gap: 30px;
    margin-top: 24px;
  }

  &__button {
    @include mixins.default-transition;
    cursor: pointer;
    background: #0078D2;
    border-radius: 4px;
    width: 100%;
    box-sizing: border-box;
    padding: 0;
    text-align: center;
    text-decoration: none;
    font-weight: 400;
    color: #FFFFFF;
    border: 1px solid #0078D2;


    &--medium {
      font-size: 16px;
      line-height: 40px;
    }

    &--large {
      font-size: 18px;
      line-height: 48px;
    }

    &:hover, &:focus {
      background-color: #0062ac;
      border: 1px solid #0062ac;
      outline: none;
    }

    &--transparent {
      background-color: transparent;
      color: #0078D2;

      &:hover, &:focus {
        background-color: rgba(145, 158, 171, 0.08);
        border: 1px solid #0062ac;
        color: #0062ac;
        outline: none;
      }
    }
  }
}

.validation-error {
  padding-top: 6px;
  font-weight: 400;
  font-size: 12px;
  line-height: 100%;
  color: #EB3333;
}

.input-group {
  display: flex;
  flex-direction: column;

  &__input {
    @include mixins.default-transition;
    border: 1px solid rgba(0, 66, 105, 0.28);
    border-radius: 5px;
    padding: 7px 12px;
    outline: none;
    box-shadow: none;

    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #002033;

    &::placeholder {
      color: rgba(0, 32, 51, 0.6);
    }

    &--invalid {
      border: 1px solid #EB3333;

      &:not([value=""]), &:focus {
        border: 1px solid rgba(0, 66, 105, 0.28);
      }
    }

    &:focus {
      border: 1px solid rgba(0, 66, 105, 0.28);
      outline: none;
      box-shadow: none;

      &::placeholder {
        color: transparent;
      }
    }

    &:hover {
      outline: none;
    }
  }
}

.select-group {
  display: flex;
  flex-direction: column;

  &__label {

  }

  &__select {
    border: 1px solid rgba(0, 66, 105, 0.28);
    border-radius: 5px;
    padding: 7px 12px;
    outline: none;
    box-shadow: none;

    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #002033;

    appearance: none;
    background: url(../../../img/dropdown.svg) right 0.75rem center/8px 10px no-repeat #fff;

    &:focus {
      border: 1px solid rgba(0, 66, 105, 0.28);
      outline: none;
      box-shadow: none;

      &::placeholder {
        color: transparent;
      }
    }

    &:hover {
      outline: none;
    }
  }
}

.textarea-group {
  display: flex;
  flex-direction: column;

  &__label {

  }

  &__textarea {
    font-family: inherit;
    border: 1px solid rgba(0, 66, 105, 0.28);
    border-radius: 5px;
    padding: 7px 12px;
    outline: none;
    box-shadow: none;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #002033;

    min-height: 98px;
    background-color: #fff;
    outline: 0;
    resize: none;

    &--invalid {
      border: 1px solid #EB3333;

      &:not([value=""]), &:focus {
        border: 1px solid rgba(0, 66, 105, 0.28);
      }
    }

    &:focus {
      color: #002033;
      background-color: #fff;
      outline: 0;
      box-shadow: none;

      &::placeholder {
        color: transparent;
      }
    }
  }
}

.checkbox-group {
  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;

    &:checked + .checkbox-group__label::before {
      border-color: #0078D2;
      background-color: #0078D2;
      background-image: url(../../../img/checkmark.svg);
    }

    /* стили при наведении курсора на checkbox */
    &:not(:disabled):not(:checked) + .checkbox-group__label:hover::before {
      background-color: rgba(145, 158, 171, 0.08);
    }

    /* стили для активного состояния чекбокса (при нажатии на него) */
    &:not(:disabled):active + .checkbox-group__label::before {
      //background-color: #0078D2;
      //border-color: #b3d7ff;
    }

    /* стили для чекбокса, находящегося в фокусе */
    &:focus + .checkbox-group__label::before {
      //box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
    }

    /* стили для чекбокса, находящегося в фокусе и не находящегося в состоянии checked */
    &:focus:not(:checked) + .checkbox-group__label::before {
      background-color: rgba(145, 158, 171, 0.08);
    }

    /* стили для чекбокса, находящегося в состоянии disabled */
    &:disabled + .checkbox-group__label::before {
      background-color: #e9ecef;
    }
  }

  & > &__label {

    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;

    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: #002033;


    &::before {
      @include mixins.default-transition;
      content: '';
      display: inline-block;
      width: 20px;
      height: 20px;
      flex-shrink: 0;
      flex-grow: 0;
      border: 1px solid rgba(0, 66, 105, 0.28);
      border-radius: 4px;
      margin-right: 12px;

      background-position: center center;
      background-size: 14px 11px;
      background-repeat: no-repeat;
    }
  }

  &__label-span {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.dropzone {
  background: transparent !important;
  border: 2px dashed rgba(0, 66, 105, 0.28) !important;
  border-radius: 4px !important;

  & > .dz-preview.dz-image-preview {
    background: transparent !important;
  }
}

.dz-image {
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  background: rgba(0, 32, 51, 0.12) !important;

  & > img{
    width: 100%;
  }
}

.dz-message {

  &__span {
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 4px;

    &--main {
      font-size: 14px;
      color: rgba(0, 32, 51, 0.6);
    }

    &--description {
      font-size: 12px;
      color: rgba(0, 32, 51, 0.3);
    }
  }

  &__button-container {
    display: flex;
    justify-content: center;
    padding-top: 12px;
  }

  &__button {
    @include mixins.default-transition;
    background: rgba(0, 66, 105, 0.07);
    border-radius: 4px;
    color: rgba(0, 57, 92, 0.8);
    font-weight: 400;
    font-size: 14px;
    line-height: 32px;
    border: none;
    margin: 0;
    padding: 0 16px 0 12px;
    display: flex;
    align-items: center;

    &:before {
      content: '';
      display: inline-block;
      background-image: url(../../../img/attach.svg);
      background-position: center center;
      background-size: 12px 12px;
      background-repeat: no-repeat;
      width: 12px;
      height: 12px;
      margin-right: 10px;
    }

    &:hover {
      background: rgba(0, 66, 105, 0.1);
    }
  }
}

.image-preview-container {
  //margin-top: 24px;
}

.image-preview {
  @include mixins.default-transition;
  width: 90px;
  height: 90px;
  margin-top: 24px;

  &__inner {
    position: relative;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    background: rgba(0, 32, 51, 0.12);

    & > img{
      width: 100%;
    }
  }

  &__delete {
    position: absolute;
    width: 12px;
    height: 12px;
    right: 5px;
    top: 5px;
    background: url(../../../img/close.svg) center center no-repeat;
    cursor: pointer;
  }
}

.show-password {
  @include mixins.default-transition;
  position: absolute;
  right: 10px;
  top: 35px;
  cursor: pointer;
  display: flex;

  &:before {
    content: '';
    background-image: url(../../../img/eye.svg);
    width: 16px;
    height: 16px;
    display: flex;
  }
}
