@use "../variables";

.datatable-pagination {
  padding-top: 32px;
}

.pagination {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;

  & > li {
    margin: 0 2px;
  }

  &__button {
    min-width: 40px;
    height: 40px;
    border-radius: 4px;

    &--active {
      background: rgba(0, 66, 105, 0.07);
    }

    &:hover {
      background: rgba(0, 66, 105, 0.07);
    }

    &--previous {
      .pagination__button-link:before {
        content: '';
        background: url(../../../img/backward.svg);
        display: flex;
        width: 16px;
        height: 16px;
        margin: 0 14px 0 0;
      }
    }

    &--next {
      .pagination__button-link:after {
        content: '';
        background: url(../../../img/forward.svg);
        display: flex;
        width: 16px;
        height: 16px;
        margin: 0 0 0 14px;
      }
    }

    &--disabled {
      display: none;
    }

    &--gap {
      & .pagination__button-link {
        cursor: unset;
      }

      &:hover {
        background: transparent;
      }
    }
  }

  &__button-link {
    cursor: pointer;
    color: variables.$typo-primary;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    text-decoration: none;
    padding: 12px 7px;

    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      text-decoration: none;
    }
  }
}