@use "../variables";
@use "../mixins";

.header {
  display: flex;
  height: 60px;
  width: 100%;
  z-index: 2;
  background: variables.$back-white;
  border-bottom: 1px solid variables.$back-border;
  padding: 0 24px;
  position: fixed;
  top: 0;
  box-sizing: border-box;
  justify-content: space-between;
}

.header-logo {
  & img {
    height: 60px;
    width: auto;
  }
}

.header-user {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  &__info {
  }

  &__name {
    font-size: 16px;
    line-height: 22px;
  }

  &__role {
    font-size: 12px;
    color: variables.$typo-secondary;
  }

  &__actions {
    display: flex;
    padding-left: 12px;
  }

  &__action-btn {
    display: flex;
    margin: 0 0 0 20px;
    padding: 0;
    background: none;
    border: none;
    cursor: pointer;

    & svg {
      width: 24px;
      height: 30px;

      & path {
        @include mixins.default-transition;
      }

      &:hover {
        & path {
          fill: variables.$typo-panel;
        }
      }
    }
  }
}