@use '../mixins';

.datatable {
  width: 100%;
  border-spacing: 0;

  border: 1px solid rgba(0, 65, 102, 0.2);
  border-collapse: collapse;

  thead {
    tr {
      background: transparent;

      th {
        border-left: 1px solid rgba(0, 65, 102, 0.2);
        border-right: 1px solid rgba(0, 65, 102, 0.2);
        font-weight: 600;
        font-size: 12px;
        line-height: 140%;
        color: #002033;
        text-align: start;
        padding: 12px;
      }
    }
  }

  tbody {
    border-top: 1px solid rgba(0, 65, 102, 0.2);

    td {
      padding: 8px 14px;
      border-left: 1px solid rgba(0, 65, 102, 0.2);
      border-right: 1px solid rgba(0, 65, 102, 0.2);

      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: #002033;

      &:first-child {
        border-left: none;
      }

      &:last-child {
        border-right: none;
      }
    }

    tr:nth-child(odd) {
      background: rgba(0, 32, 51, 0.05);
    }
  }
}

.action-btn {
  @include mixins.default-transition;
  border: none;
  padding: 0;
  margin: auto;
  cursor: pointer;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  text-decoration: none;

  &__tooltip {
    white-space: nowrap;
    visibility: hidden;
    background: #FFFFFF;
    box-shadow: 0px 2px 2px rgba(0, 32, 51, 0.02), 0px 16px 16px rgba(0, 32, 51, 0.04), 0px 4px 24px rgba(0, 32, 51, 0.04), 0px 30px 50px rgba(0, 32, 51, 0.06);
    text-align: center;
    border-radius: 4px;
    padding: 6px 7px 8px;
    position: absolute;
    z-index: 1;
    bottom: 20px;

    font-weight: 400;
    font-size: 12px;
    line-height: 120%;
    color: #002033;
  }

  &__tooltip-tail-container {
    position: absolute;
    right: calc(50% - 6px);
    overflow: hidden;
    width: 12px;
    height: 6px;
    bottom: calc(-50% + 8px);
  }

  &__tooltip-tail {
    pointer-events: none;
    width: 8.5px;
    height: 8.5px;
    padding: 0;
    margin: -4.5px auto 0;
    transform: rotate(45deg);
    background-color: #ffffff;
  }

  &:hover {
    .action-btn__tooltip {
      visibility: visible;
    }
  }
}