.main-panels {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  max-width: 960px;
  margin: auto;
  gap: 30px;

  & a {
    text-decoration: none;
  }

  &__item {
    display: block;
    overflow: hidden;
    position: relative;
    background: #0078D2;
    width: 300px;
    min-height: 169px;
    border-radius: 4px;
    cursor: pointer;

    img {
      width: 56px;
      height: 56px;
      position: absolute;
      right: 9px;
      bottom: 11px;
    }

    p {
      margin: 0;
      position: relative;
      width: 215px;
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 100%;
      text-indent: 0;
      padding: 20px 0 0 16px;
      color: #fff;
    }

    &:before {
      content: '';
      position: absolute;
      right: -32px;
      bottom: -32px;
      background: #3AB8FF;
      border-radius: 50%;
      box-shadow: 0.375em 0.375em 0 0 rgba(15, 28, 63, 0.125);
      width: 9em;
      aspect-ratio: 1 / 1;
      -webkit-transition: all .90s ease;
      -moz-transition: all .90s ease;
      -o-transition: all .90s ease;
      transition: all .90s ease;
    }

    &:hover {
      &:before {
        right: -50%;
        bottom: -50%;
        width: 200%;
      }
    }
  }
}