@use '../mixins';

.admin-actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 40px;

  &__search-input {
    background: #FFFFFF;
    border: 1px solid rgba(0, 66, 105, 0.28);
    border-radius: 4px;

    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #002033;
  }

  &__form {
    width: 300px;
  }

  &__link {
    @include mixins.default-transition;

    background: #0078D2;
    border-radius: 4px;
    text-decoration: none;
    display: flex;

    font-weight: 400;
    font-size: 18px;
    line-height: 40px;
    color: #FFFFFF;
    padding: 0 62px;

    &:hover {
      background-color: #0062ac;
    }
  }
}

.search-input {
  padding: 7px 11px;
  display: flex;
  align-items: center;
  background: #FFFFFF;
  border: 1px solid rgba(0, 66, 105, 0.28);
  border-radius: 4px;

  &__input {
    background: transparent;
    border: none;
    outline: none;
    width: 100%;

    padding: 0 10px 0 0;

    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #002033;

    &:focus {
      &::placeholder {
        color: transparent;
      }
    }

    &::placeholder {
      color: #002033;
    }
  }

  &__btn {
    background: transparent;
    padding: 0;
    margin: 0;
    border: none;
    cursor: pointer;
    display: flex;
  }
}

.admin-table {
  margin-top: 32px;
}