.notifyjs-corner {
  top: 100px !important;
  right: 60px !important;
}

.notifyjs-custom-notification-base {
  width: 400px;
  box-shadow: 0px 4px 4px rgba(0, 32, 51, 0.04), 0px 8px 24px rgba(0, 32, 51, 0.12);
  border-radius: 8px;
}

.notifyjs-custom-notification-info {
  background: #0BA5FF;
}

.notifyjs-custom-notification-error {
  background: #FF4443;
}

.custom-notification {

  &__inner {
    padding: 16px 16px 20px 16px;
  }

  background: #0BA5FF;
  box-shadow: 0px 4px 4px rgba(0, 32, 51, 0.04), 0px 8px 24px rgba(0, 32, 51, 0.12);
  border-radius: 8px;


  &__message {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: #FAFAFA;
  }
}