@use '../variables';
@use '../mixins';

.page-content {
  box-shadow: variables.$card-shadow;
  background: variables.$back-white;
  padding: 48px 78px 36px 32px;
  border-radius: 4px;
  display: flex;

  &__nav {
    width: 216px;
  }

  &__content {
    padding-left: 52px;
    flex: 1;
    overflow: hidden;

    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: rgba(0, 32, 51, 0.6);

    & h2 {
      font-weight: 700;
      font-size: 24px;
      line-height: 130%;
      color: #002033;
    }

    & h3 {
      font-weight: 700;
      font-size: 16px;
      line-height: 150%;
      color: #002033;
    }

    & img {
      width: 100%;
      filter: drop-shadow(0px 2px 2px rgba(0, 32, 51, 0.02)) drop-shadow(0px 16px 16px rgba(0, 32, 51, 0.04)) drop-shadow(0px 4px 24px rgba(0, 32, 51, 0.04)) drop-shadow(0px 30px 50px rgba(0, 32, 51, 0.06));
      border-radius: 4px;
    }
    
    & .img-magnific-popup {
      cursor: pointer;
    }

    & > .js-slider {
      border: none;
      padding: 0;
    }

    & .js-image-caption {
      text-align: center;
    }

    & > figure {
      text-indent: 0;
      margin: 0;
      width: 100% !important;

      & figcaption {
        text-align: center;
      }
    }

    & table {
      width: 100%;
      border-spacing: 0;
      border-collapse: collapse;

      tbody {
        tr:first-child {
          td {
            border-top: none;
          }
        }

        tr:last-child {
          td {
            border-bottom: none;
          }
        }

        td {
          padding: 8px 14px;
          border: 1px solid rgba(0, 65, 102, 0.2);

          &:first-child {
            border-left: none;
          }

          &:last-child {
            border-right: none;
          }
        }
      }
    }
  }

  &__content-button {
    @include mixins.default-transition;
    margin: 24px auto 0;
    border: none;
    display: flex;
    background: #0078D2;
    border-radius: 4px;
    cursor: pointer;
    padding: 0 22px;
    font-weight: 400;
    font-size: 18px;
    line-height: 48px;
    color: #FFFFFF;

    &:hover {
      background-color: #0062ac;
    }
  }
}


.treeview-nav {
  position: sticky;
  top: 80px;

  ul {
    list-style: none;
    padding: 0;
  }

  li {
    padding-left: 6px;
    border-left: 2px solid transparent;

    a {
      padding: 4px 0;
      display: block;
      text-decoration: none;
      color: #002033;

      &:hover {
        color: #0078D2;
      }
    }

    & > ul > li {
      margin-left: -8px;
      padding-left: 18px;
    }

    &.active {
      border-left: 2px solid #0078d2;

      & > a {
        color: #0078D2;
      }

      & > ul > li {
        border-left: 2px solid #fff;
      }
    }
  }
}
