.js-image-caption {
  text-align: center;
}

.js-slider {
  position: relative;
  border: 2px dashed rgba(0, 66, 105, .28);
  padding: 15px;
}

.swiper-slide {
  width: 100% !important;
  height: 100% !important;
  position: relative !important;
  text-align: center;
  margin: auto;

  & > img {
    width: 100% !important;
    max-width: calc(100% - 120px);
  }
}

.swiper-button-next {
  transform: rotate(180deg);
}

.swiper-button-next:after, .swiper-button-prev:after {
  background: url(../../../img/swiper_prev.svg);
  width: 8px;
  height: 12px;
  content: "";
  color: transparent;
}

.mfp-arrow {
  opacity: 1 !important;

  &-right {
    &:before {
      border: none !important;
    }

    border: none !important;

    &:after {
      transform: rotate(180deg);
      margin-top: 29px !important;
    }
  }

  &-left {
    &:before {
      border: none !important;
    }

    border: none !important;
  }

  &:after {
    opacity: 1;
    background: url(../../../img/swiper_prev_gallery.svg) no-repeat !important;
    background-size: 24px 36px !important;
    width: 24px !important;
    height: 36px !important;
    content: "" !important;
    color: transparent;
    border: none !important;

  }

  &:before {
    display: none;
    opacity: 1;
    //background: url(../../../img/swiper_prev.svg) no-repeat !important;
    //width: 8px !important;
    //height: 12px !important;
    //content: "" !important;
    //color: transparent;
    //border: none !important;
  }
}

.custom-mfp-arrow {
  &:before {
    background: url(../../../img/swiper_prev.svg) 30px 30px no-repeat !important;
    //width: 8px !important;
    //height: 12px !important;
    content: "" !important;
    color: transparent;
    border: none;
  }
}