@use "variables";
@use "fonts";
@use "mixins";

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  background: variables.$back-secondary;
  font-size: 14px;
  color: variables.$typo-primary;
  display: flex;
  flex-direction: column;
  font-family: variables.$font-family-base;
}

h1 {
  font-size: 32px;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  color: variables.$typo-primary;
}

.content {
  margin-left: 265px;
  padding: 42px 21px 42px 40px;
  max-width: 1180px;
  //min-width: 1180px;

  h1 {
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 150%;
    margin-top: 24px;
    margin-bottom: 48px;
  }

  &--main {
    padding: 42px 0;
    margin: 0 auto;
    max-width: 960px;
    min-width: 960px;

    h1 {
      margin-top: 0;
      margin-bottom: 42px;
    }
  }

  &--admin {
    padding: 42px 21px 42px 50px;
    max-width: 850px;
    min-width: 850px;

    h1 {
      font-weight: 600;
      font-size: 24px;
      margin-top: 0;
      margin-bottom: 24px;
    }
  }
}

.container {
  margin-top: 60px;

  &--login {
    margin: auto;
  }
}

.page-title {
  &--edit-page {
    padding-top: 40px;
  }
}
