@use '../variables';
@use '../mixins';

.panels {
  display: flex;
  gap: 20px;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 32px;
  //display: grid;
  //grid-template-columns: 1fr 1fr 1fr;
  //max-width: 960px;
  //margin: auto;
  //gap: 30px;

  & a {
    @include mixins.default-transition;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 100%;
    //color: black;
    cursor: pointer;
    text-decoration: none;
    //border: 1px solid #3AB8FF;
    //color: #00a3ff;

    &:hover {
      border-radius: 4px;
      //border: 1px solid #00a3ff;
      box-shadow: 0px 2px 2px rgba(0, 32, 51, 0.02), 0px 16px 16px rgba(0, 32, 51, 0.04), 0px 4px 24px rgba(0, 32, 51, 0.04), 0px 30px 50px rgba(0, 32, 51, 0.06);
    }
  }

  &__item {
    display: block;
    overflow: hidden;
    position: relative;
    background: #3AB8FF;
    width: 300px;
    min-height: 169px;
    border-radius: 4px;
    cursor: pointer;

    img {
      width: 56px;
      height: 56px;
      position: absolute;
      right: 9px;
      bottom: 11px;
    }

    p {
      margin: 0;
      position: relative;
      width: 215px;
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 100%;
      text-indent: 0;
      padding: 20px 0 0 16px;
      color: #fff;
    }
  }
}

