@use '../variables';
@use '../mixins';

.login {
  box-sizing: border-box;
  width: 365px;
  box-shadow: variables.$card-shadow;
  background: variables.$back-white;
  padding: 48px 32px 48px;
  position: relative;
  z-index: 0;
  border-radius: 8px;

  &__logo {
    margin-bottom: 55px;
    padding-top: 35px;
  }

  &__logo-img {
    width: 100%;
  }
}

.login-note {
  width: 365px;
  margin: 48px 0;
  text-align: center;
  line-height: 18px;
  color: variables.$typo-ghost;

  &__link {
    @include mixins.default-transition;
    color: #007bff;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
}