@use '../mixins';

.search {
  background: #FFFFFF;
  box-shadow: 0px 2px 2px rgba(0, 32, 51, 0.02), 0px 16px 16px rgba(0, 32, 51, 0.04), 0px 4px 24px rgba(0, 32, 51, 0.04), 0px 30px 50px rgba(0, 32, 51, 0.06);
  border-radius: 4px;

  &__inner {
    padding: 48px 78px;
  }

  &__form {
    display: flex;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;

    h2 {
      font-weight: 600;
      font-size: 24px;
      line-height: 120%;
      color: #002033;
      margin-right: 32px;
    }
  }

  &__input {
    @include mixins.default-transition;
    background: #FFFFFF;
    border: 1px solid rgba(0, 66, 105, 0.28);
    border-radius: 4px;
    outline: none;
    box-shadow: none;
    padding: 12px;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #002033;
    display: flex;
    flex: 1;

    &:focus {
      border: 1px solid rgba(0, 66, 105, 0.28);
      outline: none;
      box-shadow: none;

      &::placeholder {
        //color: transparent;
      }
    }

    &:hover {
      outline: none;
    }
  }

  &__form-btn {
    @include mixins.default-transition;
    margin-left: 24px;
    border: none;
    cursor: pointer;
    background: #0078D2;
    border-radius: 4px;
    color: #FFFFFF;
    font-weight: 400;
    font-size: 18px;
    line-height: 48px;
    padding: 0 42px;

    &:hover {
      background-color: #0062ac;
    }
  }
}

.search-result-table {
  margin-top: 70px;

  & > .datatable > thead > tr > th {
    font-size: 14px;
  }
}

.search-result-empty {
  margin-top: 25px;
  background: #FFFFFF;
  box-shadow: 0 2px 2px rgba(0, 32, 51, 0.02), 0 16px 16px rgba(0, 32, 51, 0.04), 0 4px 24px rgba(0, 32, 51, 0.04), 0 30px 50px rgba(0, 32, 51, 0.06);
  border-radius: 4px;

  &__inner {
    padding: 40px 80px;

    & > h2 {
      margin: 0;
      padding: 0;
      font-weight: 600;
      font-size: 24px;
      line-height: 130%;
      color: #002033;
    }

    & > p {
      margin: 0;
      padding: 18px 0 0 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: rgba(0, 32, 51, 0.6);

      & > a {
        @include mixins.default-transition;
        font-weight: 600;
        color: #0078D2;
        text-decoration: none;
        cursor: pointer;
      }
    }
  }
}