@use "../variables";
@use "../mixins";

.feedback-button {
  @include mixins.default-transition;
  position: fixed;
  display: flex;
  width: 56px;
  height: 56px;
  right: 24px;
  bottom: 48px;
  background: #0078D2;
  border-radius: 4px;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
  z-index: variables.$feedback-button-z-index;

  &:hover {
    background-color: #0062ac;
    border-color: #005b9f;
  }
}